import React from 'react'
import './Video.scss'

import ArtShow from '../ArtShow/ArtShow'


class Video extends React.Component{
    constructor(props) {
        super(props);

        this.state = {
            isFinish: false,
            showCover: false,
            swiperIndex: 1,
            isPlayMusic: true,

        }


        this.next = this.next.bind(this);

    }

    next(){
        console.log(`next`);
        this.setState({
            isFinish: true
        })
    }

    toggleMusic = () => {
        this.setState({
            isPlayMusic: !this.state.isPlayMusic
        },()=>{
            console.log(`props`,this.state.isPlayMusic);
            if( !this.state.isPlayMusic ){
                this.props.pauseMusicP();
            }else{
                this.props.playMusicP();
            }

        })
    }

    componentDidMount() {
        // setTimeout(()=>{
        //     this.setState({
        //         showCover: false
        //     })
        // },2500)
        setInterval(()=>{
            if( this.state.swiperIndex < 9 ){
                if( this.state.swiperIndex == 4 ){
                    this.setState({
                        swiperIndex: 6
                    })
                }else{
                    this.setState({
                        swiperIndex: this.state.swiperIndex + 1
                    })
                }

            }else{
                this.setState({
                    swiperIndex: 1
                })
            }
        },3000)
    }


    render() {
    const {showCover} = this.state;

        return <div className={'video'} >
            {
                this.state.isFinish?(<ArtShow />):(
                    <>
                        <div className="tv-description">庆祝中国共产党建党100周年书法摄影展开幕花絮</div>
                        <div className="tv" >
                            <div className="tv-screen">
                                <div className="cover" style={{display: showCover?'block':'none'}}>Loading...</div>
                                <img src={`https://hmsbank.oss-cn-beijing.aliyuncs.com/images/swiper/0${this.state.swiperIndex}.jpg`} title="加载中" alt="加载中请稍后..."/>
                            </div>

                        </div>
                        <div className="handle-area-video" onClick={this.next}>
                            点击观展
                        </div>
                    </>
                )
            }
            <div className={`musicIcon ${this.state.isPlayMusic?'fa-spin':''}`} onClick={this.toggleMusic}>
                <img src="https://hmsbank.oss-cn-beijing.aliyuncs.com/images/icon/music.png" alt=""/>
            </div>

        </div>
    }

}

export default Video;
