import React from 'react'
import config from "../../assets/js/config";
import './Porch.scss'
import Video from "../Video/Video";


class Porch extends React.Component{
    constructor(props) {
        super(props);

        this.state = {
            isFinish: false,
            isShowPause: 0
        }

        this.next = this.next.bind(this);

    }

    play = (e) =>{
        const audio = e.target.children[0];
        audio.play();
    }

    next = (e) => {
        this.refs.playWord.play()

        this.setState({
            isFinish: true
        })
    }

    enter = () =>{ //连续点击十次允许进入
        this.setState({
            isShowPause: this.state.isShowPause  + 1
        },()=>{
            console.log(this.state.isShowPause)
        })
    }

    playMusicP = () =>{
        this.refs.playWord.play()
    }

    pauseMusicP = () => {
        this.refs.playWord.pause()
    }

    render() {
        return <div className={`porch `}>
            <div className={`left ${this.state.isFinish?'left-finish':''}`}></div>
            <div className={`right ${this.state.isFinish?'right-finish':''}`}></div>
            <audio src="https://hmsbank.oss-cn-beijing.aliyuncs.com/bgm.mp3" id="bgm" type="audio/mpeg" ref="playWord" ></audio>
            {
                this.state.isFinish?(<Video playMusicP={this.playMusicP} pauseMusicP={this.pauseMusicP} />):
                (
                    <div className="handle-area-porch" onClick={this.next}>

                    </div>
                )
            }



            {
                 this.state.isShowPause == 10 ?null : (
                     <div className="pause-wrapper" onClick={this.enter}>
                         <p>线上展览</p>
                         <p>即将开放</p>
                         <p>敬请期待!</p>
                     </div>
                 )
            }


        </div>
    }

}

export default Porch;
